@import '../commonStyle/variables.scss';

.speed-drawer {
  position: fixed;
  z-index: 999999999999;
  width: 0;
  height: 0;
  background: #fff;
  color: #000;
  display: 'none';

  &-content {
    width: 100%;
    padding: 15px;
  }

  &-close {
    font-size: 20px;
    color: #000;
    cursor: pointer;
  }

  &-mask {
    position: fixed;
    z-index: 2;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: #000;
    width: 100%;
    height: 100%;
  }

  &-left {
    top: 0;
    left: 0;
  }

  &-right {
    top: 0;
    right: 0;
  }

  &-top {
    top: 0;
    left: 0;
  }

  &-bottom {
    bottom: 0;
    left: 0;
  }

  &-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 80px;
    padding: 15px;
    border-bottom: 1px solid $gray-400;
  }

  &-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    width: 100%;
    padding: 15px;
    z-index: 22;
    bottom: 0;
    border-top: 1px solid $gray-400;
  }
}