@import "../commonStyle/variables.scss";

.speed-tag {
	display: flex;
	align-items: center;
	justify-content: center;
	background: #eeeeee60;
	border-radius: 5px;
	border: 1px solid #aaa;
	cursor: pointer;
	padding: 10px;
	&-close {
		margin-left: 5px;
	}

	&-icon {
		margin-right: 5px;
	}
}
