@import "../commonStyle/variables.scss";

.speed-slider {
	height: 10px;
	box-sizing: content-box;
	display: flex;

	&-content {
		width: 100%;
		height: 10px;
		margin: none;
		border-radius: 10px;
		position: relative;
		background: $gray-300;

		&-disabled {
			background: $gray-300;
		}
	}

	&-select {
		position: absolute;
		left: 0;
		top: 0;
		border-radius: 10px;
		width: 50px;
		height: 10px;
		background: $green;
		height: 100%;
		z-index: 9;

		&-disabled {
			background: $gray-500;
		}
	}

	&-gan {
		position: absolute;
		left: 0;
		top: 50%;
		width: 20px;
		height: 20px;
		background: $white;
		border: 3px solid $green;
		border-radius: 50%;
		z-index: 9;
		transform: translateY(-50%);
		cursor: pointer;

		&:hover {
			box-shadow: 0 0 20px -5px rgb(13, 75, 4);
		}

		&-disabled {
			border: 3px solid $gray-500;
			cursor: not-allowed;
		}
	}

	&-tips {
		position: absolute;
		bottom: 20px;
		width: 50px;
		height: 50px;
		border-radius: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		transform: translateX(-50%);
		background: #fff;
		box-shadow: 0 0 20px -5px rgb(198, 198, 198);
	}
}
